@media only screen and (max-width: 500px) {
    .navbar {
        padding: .5rem 8px;
        height: 80px;
    }
    .navbar .one{ order: 1;} 
    .navbar .two{ order: 2;} 
    .navbar .three{ display: none;} 
    .navbar-collapse {
        position: absolute;
        background: #dedede;
        top: 80px;
        z-index: 99999999999;
        padding-top: 16px;
        left: 0;
        width: 100% !important;
        border-bottom: 1px solid #dedede;
    }
    .navbar .nav-link {
        color: #222!important;
    }
    .navbar .navbar-nav {
        gap: 0;
    }
    .navbar-collapse a {
        margin-bottom: 0;
    }
    .navbar .logo img{
        width: 50px;
        height: 50px;
    }
    .top-navbar{
        padding: 5px 8px;
    }
}