.contact{
  padding: 2rem;
  margin-top: 9rem;
}
.contact .contact-header{
  margin-bottom: 48px;
}
.contact .contact-title{
  font-size: 48px;
  margin-bottom: 24px;
  font-weight: 700;
}
.contact .contact-subtitle{
  font-size: 24px;
  margin-bottom: 0;
  font-weight: 400;
}
.contact .contact-social{
  border: 1px solid rgba(255, 202, 186, 1);
  background-color: rgba(236, 243, 252, 1);
  border-radius: 20px;
  padding: 24px;
  margin-bottom: 64px;
}
.contact .contact-social img{
  margin-bottom: 38px;
}
.contact .contact-social .social-title{
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 16px;
}
.contact .contact-social .social-subtitle{
  font-size: 16px;
  color: rgba(98, 99, 108, 1);
  margin-bottom: 24px;
}
.contact .contact-social .social-value{
  margin-bottom: 0;
  font-size: 20px;
  text-decoration: underline;
  font-weight: 700;
  color: #000;
}
.contact .css-13cymwt-control{
  height: 44px;
  background: none;
}
.contact .join-us-form{
  background-color: rgba(250, 247, 245, 1);
  padding: 32px 20px;
  border-radius: 20px;
}
.contact .suggestion-form{
  padding: 32px 20px 70px;
  border-radius: 20px;
  border: 1px solid rgba(205, 206, 215, 1);
}
.contact .suggestion-form .contact-tabs{
  display: flex;
  gap: 16px;
  margin-bottom: 24px
}
.contact .suggestion-form .contact-tabs .tab{
  font-size: 24px;
  font-weight: 700;
  padding-bottom: 8px;
  cursor: pointer;
  margin-bottom: 0;
}
.contact .suggestion-form .contact-tabs .tab.active{
  color: var(--blue-color);
  border-bottom: 1px solid var(--blue-color);
}
.contact .suggestion-form textarea{
  border: 1px solid rgba(205, 206, 215, 1);
  border-radius: 10px;
  padding: 14px 12px;
  width: 100%;
  display: block;
  height: 85px;
}
.contact .join-us-form input{
  background: none;
}
.contact .join-us-form .getInTouch{
  color: #000;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 24px;
}
.contact .apply{
  background-color: var(--blue-color);
  color: #fff;
  border-radius: 8px;
  margin-top: 32px;
  width: 100%;
  height: 40px;
  line-height: 1;
}

