@media only screen and (max-width: 500px) {
  .contact {
    padding: 1rem;
    margin-top: 7rem;
  }
  .contact .row div:nth-of-type(3){
    margin-top: 2rem;
  }
  .login-form, 
  .signup-form {
    padding: 1rem 0;
  }
}