.packages{
  min-height: 100vh;
  margin-top: 208px;
  overflow: hidden;
  padding-bottom: 40px;
}
.type-title{
  font-size: 32px;
  line-height: 43.39px;
  font-weight: 800!important;
  margin-bottom: 24px;
}
.type-title span{
  font-size: 18px;
  font-weight: 400;
  line-height: 20.93px;
  color: #848484;
}
.package{
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
  border-radius: 30px;
  overflow: hidden;
}
.package img{
  width: 100%;
  height: 223px;
}
.package .package-info{
  display: flex;
  justify-content: space-between;
  padding: 16px 10px;
}
.package .package-info{
  display: flex;
  justify-content: space-between;
  padding: 16px 10px;
  align-items: center;
  gap: 10px;
}
.package .package-title{
  font-size: 24px;
  margin: 0;
  font-weight: 700;
}
.package .package-price{
  font-size: 20px;
  color: var(--orange-color);
  margin: 0;
  font-weight: 700;
}
.package .grades-div,
.package .subjects-div,
.package .features-div{
  padding: 0 10px;
}
.package .grades-div .grades-title,
.package .subjects-div .subject-title,
.package .features-div .features-title{
  font-size: 16px;
  margin-bottom: 12px;
  font-weight: 700;
  color: rgba(98, 99, 108, 1);
}
.package .features-div .features{
  padding-left: 1.25rem;
}
.package .grades-div .grades,
.package .subjects-div .subjects{
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 9px;
  margin-bottom: 16px;
}
.package .grades-div .grades span,
.package .subjects-div .subjects span{
  text-align: center;
  border-radius: 20px;
  background: rgba(205, 225, 254, 1);
  padding: 4px 0;
  font-size: 12px;
}
.package .subjects-div .subjects span{
  background-color: rgba(255, 202, 186, 1);
}
.package .add-to-cart{
  padding: 0 10px 20px;
}
.package .add-to-cart button{
  background-color: var(--blue-color);
  border-color: var(--blue-color);
  font-size: 16px;
  font-weight: 700;
  border-radius: 10px;
}
@media only screen and (max-width: 500px) {
  .packages{
    margin-top: 24px!important;
  }
  .type-title{
    font-size: 24px;
  }
}