.blog{
  margin-top: 0;
  margin-bottom: 40px;
}
.blog .blog-header{
  position: relative;
}
.blog .blog-header img{
  height: 650px;
  width: 100%;
}
.blog .blog-date{
  font-size: 20px;
  color: rgba(98, 99, 108, 1);
  margin-bottom: 24px;
  margin-top: 32px;
}
.blog .blog-title{
  font-size: 32px;
  font-weight: 600;
  color: #000;
  margin-bottom: 24px;
}
.blog .blog-description{
  font-size: 16px;
  color: rgba(98, 99, 108, 1);
  margin-bottom: 48px;
}