.header-home{
    position: relative;
    height: 100vh;
    overflow: hidden;
}
.header-home .bg{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
}
.header-home .icons{
    position: absolute;
    bottom: 32px;
    right: 75px;
    display: flex;
    flex-direction: column;
    gap: 24px;
}
.header-home .buttons{
    position: absolute;
    top: 50%;
    width: 100%;
    display: flex;
    justify-content: space-around;
}
.header-home .buttons button{
    padding: 12px 80px;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 700;
    display: flex;
    gap: 8px;
    align-items: center;
}
.header-home .buttons .fun{
    background-color: var(--orange-color);
}
.header-home .buttons .learn{
    background-color: var(--blue-color);
}
.ar .header-home .buttons .fun img,
.ar .header-home .buttons .learn img{
    transform: rotate(180deg);
}