.aboutUs{
  margin-top: 0;
  margin-bottom: 40px;
}
.aboutUs .about-header{
  position: relative;
}
.aboutUs .about-header .header-title{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 100px;
  color: #fff;
  font-weight: 700;
}
.aboutUs .about-header img{
  height: 650px;
  width: 100%;
}
.aboutUs .about-title{
  font-size: 32px;
  margin-top: 48px;
  font-weight: 600;
  color: #000;
  margin-bottom: 24px;
}
.aboutUs .about-description{
  font-size: 16px;
  color: rgba(98, 99, 108, 1);
  margin-bottom: 48px;
}
.aboutUs .details{
  background-color: rgba(236, 243, 252, 1);
  border-radius: 30px;
  padding: 24px;
  display: flex;
  justify-content: space-around;
}
.aboutUs .details .det-info div{
  display: flex;
  gap:10px;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
}
.aboutUs .details .det-info div span{
  font-size: 32px;
  font-weight: 700;
}
.aboutUs .details .det-info p{
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 0;
}
.aboutUs .details .det-info img{
  background-color: #fff;
  border-radius: 50%;
  padding: 10px;
}
.aboutUs .details .det-info{
  text-align: center;
}