.footer{
    color: #fff;
    padding: 0 88px;
}
.default-section{
    border-bottom: 1px solid #D0D0D0;
    padding-bottom: 32px;
    padding-top: 32px;
}
.footer .titles{
    color: #fff;
    margin-bottom: 16px;
    font-weight: 700;
    line-height: 24.41px;
    font-size: 16px;
}
.footer .nav-link{
    padding: 0;
    margin-bottom: 16px;
}
.footer .two{
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;   
}
.footer a{
    display: block;
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
}
.footer .contacts-title{
    font-size: 18px;
    font-weight: 400;
    line-height: 21.64px;
}
.footer .contacts-title{
    font-size: 18px;
    font-weight: 400;
    line-height: 21.64px;
    margin-bottom: 24px;
}
.footer .contacts p{
    font-size: 14px;
    font-weight: 400;
    line-height: 16.83px;
    margin-bottom: 22.5px;
    display: flex;
    gap: 3.5px;
    align-items: center;
}
.footer .description{
    width: 75%;
    margin: auto;
}
.footer .description img{
    margin-bottom: 12px;
}
.footer .description p{
    font-size: 10px;
    line-height: 15px;
    font-weight: 400;
}
.footer .follow{
    font-size: 18px;
    line-height: 21.64px;
    margin-bottom: 24px;
}

.footer .icons{
    display: flex;
    gap: 16px;
}
.footer .links a{
    margin-bottom: 16px;
}
.footer .contacts a{
    color: #fff;
    font-size: 14px;
    text-decoration: none;
}
.footer .links .address{
    margin-top: 32px;
    font-size: 12px;
    line-height: 18px;
    display: flex;
    gap: 10px;
}
.footer .links .address span{
    display: block;
    width: fit-content;
}
.footer .power-div{
    padding: 16px 0;
}
.footer .power{
    font-size: 14px;
    line-height: 21px;
}
.footer .power a{
    font-size: 16px;
    line-height: 19.23px;

}
.footer .one{ order: 1;}
.footer .two{ order: 2;}
.footer .three{ order: 3;}
.footer .four{ order: 4;}
@media only screen and (max-width: 500px) {
    .footer {
        padding: 24px 20px 0;
        margin-top: 30px;
    }
    .footer .description {
        width: 95%;
    }
    .footer .one{ order: 1;}
    .footer .two{ order: 2;}
    .footer .three{ order: 3;}
    .footer .titles{
        margin-bottom: 12px;
    }
    .links{
        margin-bottom: 1rem!important;
    }
    .power-div{
        flex-direction: column;
        padding: 12px 0;
    }
    .footer .links .address {
        margin-top: 20px;
    }
    .footer .links .address span,
    .footer .links a{
        font-size: 18px!important;
    }
    .footer .two a,
    .footer .three a{
        margin-bottom: 8px;
    }
}

.st0{fill:#a6a6a6}
.st1{fill:#fff}
.st2{fill:url(#SVGID_1_)}
.st3{fill:url(#SVGID_2_)}
.st4{fill:url(#SVGID_3_)}
.st5{fill:url(#SVGID_4_)}
.st6,.st7,.st8{opacity:.2;enable-background:new}
.st7,.st8{opacity:.12}
.st8{opacity:.25;fill:#fff}
.st9{fill:#ffffff;stroke:#ffffff;stroke-width:.2;stroke-miterlimit:10}