.blog-card{
    position: relative;
    height: auto;
    text-decoration: none;
    border-radius: 20px;
}
.blog-card .arrowIcon {
    width: 32px;
    height: 32px;
}
.blog-card .blog-info{
    padding: 16px;
}
.blog-card img{
    height: 300px;
    border-radius: 20px 20px 0 0;
}
.blog-card .title{
    font-size: 16px;
    margin-bottom: 12px;
    font-weight: 700;
    color: #111;
    text-decoration: none;
    text-align: start;
}
.blog-card .description{
    color: rgba(98, 99, 108, 1);
    font-size: 12px;
    margin-bottom: 16px;
    text-align: start;
}
.blog-card .read-time{
    color: var(--orange-color);
}

@media only screen and (max-width: 850px) {
    .blog-cardimg{
      height: 100px;
    }
}
@media only screen and (max-width: 500px) {}