*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary-color: #F38B35;
  --bg-primary-color: #F7F9FF;
  --orange-bg-color: #f38b3517;
  --footer-bg: #343434;
  --navbar-top-bg: rgba(40, 193, 229);
  --badge-bg: rgba(99, 142, 200, 1);
  --orange-color: rgba(245, 133, 57, 1);
  --blue-color: rgba(15, 80, 166, 1);
  --bg-color: rgba(0, 0, 153, 1);
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.text-primary{
  color: var(--primary-color) !important;
}
.ltr{
  direction: ltr;
}
.rtl{
  direction: rtl;
}
.loader {
    width: 50px;
    margin: auto;
    aspect-ratio: 1;
    display: grid;
    border: 4px solid #0000;
    border-radius: 50%;
    border-right-color: var(--primary-color);
    animation: l15 1s infinite linear;
}
.loader::before,
.loader::after {    
    content: "";
    grid-area: 1/1;
    margin: 2px;
    border: inherit;
    border-radius: 50%;
    animation: l15 2s infinite;
}
.loader::after {
    margin: 8px;
    animation-duration: 3s;
}
@keyframes l15{ 
    100%{transform: rotate(1turn)}
}
.opacity-bg{
  background: rgba(30, 31, 36, 0.5);
}
.fun-bg{
  background-color: var(--orange-color);
}
.learn-bg{
  background-color: var(--bg-color);
}
.mix-bg{
  background: linear-gradient(90.13deg, #0F50A6 -8.99%, #F58539 95.94%);
}